@use "config/variables" as var;

html {
  // font-size: 10px;
}
body{
  font-family:  -apple-system, BlinkMacSystemFont, "Segoe UI", "Noto Sans TC", "Microsoft JhengHei", "Helvetica Neue", Arial, sans-serif;
  line-height: 1.5;
  font-size: 1.6rem;
  font-weight: 400;
}
:focus{ outline: 0;}
::selection { 
  color: var.$white; 
  background-color: var.$primary;
}
img {
  max-width: 100%;
  height: auto;
  display: block;
}
h1, h2, h3, h4, h5, h6{
  line-height: 1.5; // 1.2
  margin: 0;
}
ul,li{
  list-style: none;
  margin: 0;
  padding: 0
}
h1 {
  font-size: 3.6rem; // 4.8rem
}
h2 {
  font-size: 2.8rem; // 3.2rem
}
h3 {
  font-size: 2rem; // 2.4rem
}
h4 {
  font-size: 1.8rem; // 2.2rem
}
h5 {
  font-size: 2rem;
}
h6 {
  font-size: 1.8rem;
}
p{
  margin: 0;
}
b {
  font-weight: 500;
}
a{
  color: var.$white; // $black
  text-decoration: none;
  word-break: break-all;
  display: inline-block;
  position: relative;
  z-index: 2;
}
